body {
  background-color: rgb(242, 239, 255);
}
.app-wrapper {
  width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas: 
  "h h"
  "n c"
  ;
}
.app-wrapper-content {
  grid-area: c;
  background-color: rgba(128, 128, 128, 0.507);
  min-height: 100vh;
  border-radius: 0 10px 10px 0;
  margin-top: 15px;
  margin-left: 15px;
}

